(function(){
  'use strict';

    apiService.$inject = ["$http", "APIURL"];
  angular
    .module('app')
    .factory('apiService', apiService);

    /*@ngInject*/
    function apiService($http, APIURL){
      var OMD_API_URL = "http://www.omdbapi.com/";
      var OMD_API_KEY = "bd9be61b";

      return {
        getMovies: getMovies,
        getSeries: getSeries,
        getGameOfThrones: getGameOfThrones,
        getFoods: getFoods
      };

      function getMovies(){
        return $http.get(OMD_API_URL + '?apikey=' + OMD_API_KEY + '&s=Star%20Wars&type=movie');
      }

      function getSeries(id){
        return $http.get(OMD_API_URL + '?apikey=' + OMD_API_KEY + '&t=Game%20of%20Thrones&Season=' +id);
      }

      function getGameOfThrones(){
        return $http.get(OMD_API_URL + '?apikey=' + OMD_API_KEY + '&t=Game%20of%20Thrones');
      }

      function getFoods(){
        return $http.get(APIURL + 'fakeApi/foods.json');
      }
    }
})();

(function(){
  'use strict';

    answerService.$inject = ["$http", "APIURL"];
  angular
    .module('app')
    .factory('answerService', answerService);

    /*@ngInject*/
    function answerService($http, APIURL){
      var movieId = '';
      var movies = '';
      var foodId = '';
      var foods = '';
      var gotSeason = '';
      
      return {
        saveMovies: saveMovies,
        saveMovieSelected: saveMovieSelected,
        getMovieSelected: getMovieSelected,
        saveFoods: saveFoods,
        saveFoodSelected: saveFoodSelected,
        getFoodSelected: getFoodSelected,
        saveGotSeason: saveGotSeason,
        getGotSeason: getGotSeason
      };

      function saveMovies(results) {
        movies = results;
      }

      function saveMovieSelected(id) {
        movieId = id;
      }

      function getMovieSelected() {
        var movieSelected = '';

        movies.map(function(movie){
          if (movie.imdbID === movieId) {
             movieSelected = movie;
          }
        });

        return movieSelected;
      }

      function saveFoods(results) {
        foods = results;
      }

      function saveFoodSelected(id) {
        foodId = id;
      }

      function getFoodSelected() {
        var foodSelected = '';

        foods.map(function(food){
          if(food.imdbID === parseInt(foodId)) {
            foodSelected = food;
          }
        });

        return foodSelected;
      }

      function saveGotSeason(id) {
        gotSeason = id;
      }

      function getGotSeason() {
        return gotSeason;
      }
    }
})();

